                return;
            }

            let phone = this.phone;
            if (!phone) {
				// this.$toast("");
				this.showPhone = true;
				return;
			}

			let type = 1; // 12
			let money = 0;
			if (this.activeIndex == 0) {
				type = 1;
				money = this.moneys[0].price;
			} else {
				type = 2;
				money = this.moneys[1].price;
			}
        
			var ua = window.navigator.userAgent.toLowerCase();
			if (ua.match(/MicroMessenger/i) == "micromessenger") {
				//
				wx.miniProgram.getEnv(res => {
					console.info("res", res);
					if (res.miniprogram) {
						wx.miniProgram.navigateTo({
							url: `/pages/wePay/wePay?money=${money}&type=${type}`,
						});
					} else {
						this.payFunc(money, type, phone);
					}
				});
			} else {
				this.$toast("");
			}
		},
		async payFunc(money, type, phone) {
			let openId = cookie.get("user_openid");
            let nickname = cookie.get("user_nickname");
            let avatar = cookie.get("user_head");

			if (!openId) {
				this.$toast("");
				return;
			}
			let res = await weixinApi.pay({
				openid: openId,
				total_fee: Math.ceil(money * 100),
			});
			console.log(openId);
			console.log(type);
			console.log(money);
			if (res.code == 200) {
				wx.ready(async () => {
					let data = res.data;
					let options = data.options;

					/**
					 * extra {out_trade_no, total_fee, prepayid}
					 * out_trade_no 
					 * total_fee 
					 * prepayid ID
					 */
					let extra = data.extra;
					let sell_type_name =
						type == 1 ? "-365" : "-1888";
					
					options.success = async () => {
						// 
						let result = await weixinApi.payOk({
							...extra,
							name: "",
							sell_type_name: sell_type_name,
							sell_type: 5,
							openid: openId,
							pay_type: 2, //  1 2 3 4
							amount: money,
                            phone,
                            nickname,
                            avatar
						});

						if (result.code == 200) {
							that.$notify({
								message: result.msg,
								color: "#ffffff",
								background: "#00B76F",
							});
							setTimeout(() => {
								// that.$router.push({
								// 	path: "/pay/success",
								// });
							}, 500);
						}
					};

					//  
					options.cancel = function () {
						console.log("");
					};
					// 
					options.fail = function () {
						console.log("");
					};
					// JSAPI
					wx.chooseWXPay(options);
				});
			}
		},
	},
};
</script>

